















import Vue from 'vue';
import {mapState} from 'vuex';
import router from '../router';
import {IStateRoot} from "@/store/interface";

export default Vue.extend({
    name: 'Header',

    computed: {
        ...mapState<IStateRoot>({
            pageTitle: state => state.pageTitle,
        }),
    },

    methods : {
        back : () => {
            router.go(-1);
        }
    }

});

