
























import Vue from 'vue';
import {mapState} from 'vuex';
import router from '../router';

export default Vue.extend({
    name: 'Footer',

    computed: {
        currentPath : function() {
            return this.$route.path;
        }
    },

    mounted : function() {

    },

    methods : {
        goToRoute(route) {
            if(route !== this.$route.path) this.$router.push(route);
        }
    }

});

